import React from 'react';
import { BrowserRouter , Route, Switch } from 'react-router-dom';
import {combineReducers} from 'redux';
import history from './history.js';

import {store} from './Store.js';
import {lazyLoader} from './utils/lazyRouterUtils.js';
import App from './App.js';

//import Home from './pages/Home.js';
//import About from './pages/About.js';
//import NotFound from './pages/NotFound.js';

const asyncHome = lazyLoader(() => import('./pages/Home.js'));

const asyncAbout = lazyLoader(() => import('./pages/About.js'));

const asyncNotFound = lazyLoader(() => import('./pages/NotFound.js'));

const asyncCounter = lazyLoader(() => import('./pages/CounterPages.js'));


const Routers = () => (
	<BrowserRouter history={history}>
		<App>
			<Switch>
				<Route exact path='/' component={asyncHome} />
				<Route path='/about' component={asyncAbout} />
				<Route path='/counter' component={asyncCounter} />
				<Route path='*' component={asyncNotFound} />
			</Switch>
		</App>
	</BrowserRouter>
);

export default Routers;


